@import url(https://fonts.googleapis.com/css?family=Passion+One);
@import url(https://fonts.googleapis.com/css?family=Poiret+One);

html {
    height: 100%;
}

body {
    min-height: 100%;
    height: inherit;
    padding: 0;
    margin: 0;

    text-align: center;
    background-color: #36393B;
}
.logo {
    position: fixed;
    top: 10px;
    left: 10px;
    z-index: 100;
}
.splash-block {
    width: 100%;
    height: 500px;

    background-color: #333;
    background-image: url('../img/logo-rails.png');
    background-image: url('../img/logo-rails.png'), radial-gradient(circle at 50% 40%, #F8CA00 0%, #E97F02 95%);
    background-size: 512px, 100%;
    background-repeat: no-repeat;
    background-position: 50% -80%;

    box-shadow: 0 0 10px rgba(0,0,0,0.3);
    position: relative;
    margin-bottom: 120px;
}

.headline-holder {
    position: absolute;
    left: 0;
    right: 0;
    bottom: -110px;
    margin: 0 auto;
    text-align: center;
}

.headline {
    color: #fff;
    background-color: #F0A830;

    border-radius: 3px;
    box-shadow: 0 0 10px rgba(0,0,0,0.3);

    text-shadow: 0 2px 4px rgba(0,0,0,0.2);
    margin: 0;
    text-align: center;

    line-height: 0.78;
    font-family: 'tertreextra_bold';
    font-weight: normal;

    display: inline-block;
    padding: 30px;

    box-sizing: border-box;
}

.headline .headline-focus {
    font-family: 'Passion One', cursive;
    font-size: 100pt;
    text-shadow: 2px 2px 2px #033649;
}

.headline .headline-getmoredone {
    font-family: 'Poiret One', cursive;
    color: #000;
    text-shadow: none;
    padding-top: 20px;
}

.headline .headline-getmoredone {
    line-height: 0.9;
    font-size: 54.5pt;
}

.intro-block {
    margin: 0 auto;
    width: 850px;
    text-align: center;
    position: relative;
}

.intro-text {
    padding: 20px 0 40px 0;
    margin: 0;
    color: #fff;
    width: 99%;
}
.intro-text h2 {
    color: #F0A830;
}

.intro-text h3 {
    margin: 24px 0 0 0;
    padding: 0;
    font-size: 24pt;
}

.intro-text p:first-child {
    font-family: 'Poiret One', cursive;
    font-size: 22px;
    margin-top: 0;
}

.intro-detail-row {
    padding-top: 30px;
    padding-bottom: 30px;
    margin-bottom: 20px;
    width: 100%;
    clear: both;
    background: radial-gradient(circle at 50% 40%, #F8CA00 0%, #E97F02 95%);
}

.intro-detail-row p {
    color: #36393B;
    text-shadow: 2px 2px 2px #e4e4e4;
    text-align: center;
    font-size: 22pt;
    font-weight: bolder;
    width: 800px;
    margin: 0 auto;
    text-align: center;
    padding: 40px 0;
}

.intro-detail-row-2 {
    padding-top: 20px;
    padding-bottom: 50px;
    margin-bottom: 20px;
    width: 100%;
    clear: both;
    background-color: #36393B;
}

.intro-detail-row-2 p {
    color: #fff;
    text-align: center;
    font-size: 22pt;
    font-weight: bolder;
    width: 800px;
    margin: 0 auto;
    text-align: center;
    padding: 40px 0;
}

.content {
    padding-top: 30px;
    padding-bottom: 10px;
    background-color: #fff;
    font-size: 18px;
}

.info {
    height: 100px;
    background-color: #000;
    padding-top: 40px;
}
.info p {
    color: #fff;
    font-size: 18px;
}

